      </md-button>
    </md-toolbar>
    <md-tabs @change="removeDefaultTheme">
      <md-tab md-label="themed by sass">
        <themed-by-sass></themed-by-sass>
      </md-tab>
      <md-tab md-label="themed by js">
        <themed-by-js></themed-by-js>
      </md-tab>
    </md-tabs>
  </div>
</template>

<script>

  import ThemedBySass from './ThemedBySass.vue'
  import ThemedByJs from './ThemedByJs.vue'
  import manager from '../../src/dynamicThemeManager'

  export default {
    data () {
      return {}
    },
    methods: {
      removeDefaultTheme: function () {
        manager.removeTheme('default')
        manager.removeTheme('style1')
        manager.removeTheme('style2')
      }
    },
    components: {
      ThemedBySass,
      ThemedByJs
    }
  }
</script>

<style>
  .md-toolbar .md-title{
    color: rgba(255,255,255,.87);
  }
  .md-title{
    color:#009688;
  }
  .md-theme-default.md-tabs>.md-tabs-navigation .md-tab-header{
    color: rgba(255,255,255,.57) !important;
  }
  .md-theme-default.md-tabs>.md-tabs-navigation .md-tab-header.md-active, .md-theme-default.md-tabs>.md-tabs-navigation .md-tab-header:focus {
    color: rgba(255,255,255,.87) !important;
  }
  code {
    color: inherit !important;
    background-color: inherit !important;
  }
  </style>


